*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 1rem;

  min-height: 100vh;
  min-width: 100%;
/* 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}
header , main , footer {
  width: 100vw;
  text-align: center;
}
main{

  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}
h1 , footer p{
  background-color: #1e1e1e;
  color: white;
  padding: 3rem;
  width: 100%;
}
footer p{
  padding: 1rem;
  position: fixed;
  bottom: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  min-height: 100vh;
 /*  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between; */

}

ul {
  width: 100%;
  list-style: none;
  padding: 0 0.25rem 0.25rem;
}
ul li::before{
  content: "\200B";
}
.item{
  background-color: #eee;
  margin: 1rem;
  font-size: 1.5rem;
  padding: 0.5rem 0 0.5rem 0.5rem;
  margin: 0.25rem 0;
  list-style-type: none;

  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.item:first-child {
  margin:0;
}
.item input[type="checkbox"]{
  text-align: center;
  width: 2.5rem;
  width: 48px;
  min-width: 48px;
  height: 2.5rem;
  height: 48px;
  min-height: 48px;
  cursor: pointer;
  margin-right: 0.5rem;
}
.item input[type="checkbox"]:focus + label {
  text-decoration: underline;
}
.item > label {
  font-size: 1.5rem;
  flex-grow: 1;
}
.item svg {
  width: 48px;
  min-width: 48px;
  height: 36px;
  font-size: 1rem;
  color: steelblue;
  cursor: pointer;
}
.item svg:focus,
.item svg:hover{
  color: red;
  outline: none;
}

.addForm {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 0.5rem 0 0;
  padding: 0 0.5rem 0.25rem;
  border-bottom: 1px solid #eee;
}

.addForm label {
  position: absolute;
  left: -99999px;
}

.addForm input[type='text'] {
  flex-grow: 1;
  max-width: calc(100% - 50px);
  min-height: 48px;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  margin-right: 0.25rem;
  outline: none;
}

button {
  height: 48px;
  min-width: 48px;
  border-radius: 0.25rem;
  padding: 0.5rem;
  font-size: 1rem;
  background-color: aliceblue;
  color: mediumblue;
  cursor: pointer;
}

button:focus, 
button:hover {
  color: white;
  background-color: limegreen;
  outline: none;
}
.searchForm {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 0.25rem 0 0;
  padding: 0 0.5rem 0.25rem;
  border-bottom: 1px solid #eee;
}

.searchForm label {
  position: absolute;
  left: -99999px;
}

.searchForm input[type='text'] {
  flex-grow: 1;
  max-width: 100%;
  min-height: 48px;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  outline: none;
}